import React from "react";
import { HeadFC, PageProps, graphql } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";

import {
  IProjectQueryResult,
  IProjectsPageContext,
} from "../types/project.type";

import ContentContainer from "../components/common/contentContainer";
import ScreenshotsCarousel from "../components/projects/screenshotsCarousel";
import { HeadSeo, LocalBusinessMetadata } from "../components/common/metadata";
import ProjectDescriptionSection from "../components/projects/projectDescriptionSection";
import StackOrTeamCard from "../components/projects/stackOrTeamCard";

const Project = ({
  data: {
    markdownRemark: { frontmatter, html },
  },
  pageContext: { markupData, lang },
}: PageProps<IProjectQueryResult, IProjectsPageContext>) => {
  const {
    title,
    description,
    challenge,
    clientDescription,
    team,
    screenshots,
    stack,
  } = frontmatter;
  const logo = getImage(frontmatter.logo);

  return (
    <div
      itemScope
      itemType="https://schema.org/CreativeWork"
      className="flex bg-main-100"
    >
      <ContentContainer
        maxWidthClass="max-w-4.5xl"
        className="flex-col text-secondary-70 py-6 md:py-16 lg:!px-0 gap-5 md:gap-8"
      >
        <div className="flex flex-col flex-col-reverse items-center md:items-start md:flex-row justify-between gap-x-16 gap-y-2.5 pb-10 gradient-border border-b border-solid">
          <div className="flex-1 w-full md:max-w-[528px]">
            <h1
              itemProp="name"
              className="w-full text-[24px] md:text-4xl font-semibold text-secondary-100 mb-5 uppercase"
            >
              {title}
            </h1>
            <p itemProp="description" className="w-full text-base font-light">
              {description}
            </p>
          </div>

          {logo ? (
            <div className="w-28 md:w-48 h-28 md:h-48 flex items-center justify-center">
              <GatsbyImage
                image={logo}
                imgStyle={{ objectFit: `contain` }}
                alt="logo"
                itemProp="image"
                className="max-h-full max-w-full grayscale"
              />
            </div>
          ) : null}
        </div>

        <ProjectDescriptionSection
          title={markupData.clientSectionTitle}
          text={clientDescription}
          itemProp="sourceOrganization"
          itemScope
          itemType="https://schema.org/Organization"
          textAttr={{
            itemProp: "description",
          }}
        />

        <ProjectDescriptionSection
          title={markupData.challengeSectionTitle}
          text={challenge}
          textAttr={{
            itemProp: "about",
          }}
        />

        {html ? (
          <ProjectDescriptionSection
            title={markupData.solutionSectionTitle}
            text={challenge}
            textAttr={{
              itemProp: "articleBody",
              dangerouslySetInnerHTML: { __html: html },
            }}
          />
        ) : null}

        {team?.length ? (
          <ProjectDescriptionSection
            title={markupData.teamSectionTitle}
            className="flex gap-2.5 flex-wrap pt-2 md:pt-0"
          >
            {team.map((member) => (
              <StackOrTeamCard
                key={member}
                id={member}
                text={member}
                itemProp="contributor"
              />
            ))}
          </ProjectDescriptionSection>
        ) : null}

        {stack?.length ? (
          <ProjectDescriptionSection
            title={markupData.techSectionTitle}
            className="flex gap-2.5 flex-wrap pt-2 md:pt-0"
          >
            {stack.map((tech) => (
              <StackOrTeamCard
                key={tech}
                id={tech}
                text={tech}
                itemProp="keywords"
              />
            ))}
          </ProjectDescriptionSection>
        ) : null}

        {screenshots?.length ? (
          <section className="w-full mb-8">
            <h2 className="text-[24px] md:text-4xl text-secondary-100 uppercase w-full max-w-[320px]">
              {markupData.resultSectionTitle}
            </h2>
            <ScreenshotsCarousel screenshots={screenshots} />
          </section>
        ) : null}
        <LocalBusinessMetadata itemProp="author" lang={lang} />
      </ContentContainer>
    </div>
  );
};

export default Project;

export const Head: HeadFC<IProjectQueryResult, IProjectsPageContext> = ({
  data: {
    markdownRemark: {
      frontmatter: { title, cover },
    },
  },
  pageContext,
  location,
}) => {
  const coverImg = getImage(cover);

  return (
    <HeadSeo
      title={`${pageContext.markupData.head} | ${title.toUpperCase()}`}
      siteUrl={pageContext.site.siteUrl}
      path={location.pathname}
      img={coverImg?.images.fallback?.src}
      lang={pageContext.lang}
    />
  );
};

export const query = graphql`
  query ($id: String) {
    markdownRemark(id: { eq: $id }) {
      id
      html
      frontmatter {
        stack
        title
        description
        challenge
        clientDescription
        logo {
          childImageSharp {
            gatsbyImageData(
              formats: [AUTO, WEBP, AVIF]
              placeholder: BLURRED
              width: 180
            )
          }
        }
        cover {
          childImageSharp {
            gatsbyImageData(
              formats: [AUTO, WEBP, AVIF]
              placeholder: BLURRED
              height: 400
            )
          }
        }
        team
        screenshots {
          id
          childImageSharp {
            gatsbyImageData(
              formats: [AUTO, WEBP, AVIF]
              placeholder: BLURRED
              height: 424
            )
          }
        }
      }
    }
  }
`;
