import React, { HTMLAttributes } from "react";

interface IProjectDescriptionSectionProps
  extends HTMLAttributes<HTMLDivElement> {
  title: string;
  className?: string;
  text?: string;
  textAttr?: HTMLAttributes<HTMLParagraphElement>;
}

const ProjectDescriptionSection = ({
  title,
  text,
  className,
  children,
  textAttr,
  ...attr
}: React.PropsWithChildren<IProjectDescriptionSectionProps>) => {
  return (
    <section {...attr} className="w-full">
      <h2 className="text-[24px] md:text-4xl text-secondary-100 uppercase w-full max-w-[320px] mb-2">
        {title}
      </h2>
      {!!textAttr?.dangerouslySetInnerHTML ? (
        <p
          {...textAttr}
          className={`text-base md:text-lg w-full max-w-[544px] float-right ${className}`}
        />
      ) : (
        <p
          {...textAttr}
          className={`text-base md:text-lg w-full max-w-[544px] float-right ${className}`}
        >
          {children ? children : text}
        </p>
      )}
    </section>
  );
};

export default ProjectDescriptionSection;
